.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19, 19, 19, 0.8);
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
}

.location p{
    line-height: 30px;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social{
    display: flex;
    margin-top: 1rem;
}

h4{
    font-size: 1.3rem;
    padding-bottom: 0.7rem;
}

p{
    font-size: 1.2rem;
}

.social > h4{
    font-size: 2rem;
}

@media screen and (max-width: 1040px) {
    .footer-container{
        grid-template-columns: 1fr;
    }

    h4{
        font-size: 2rem;
    }

    p{
        font-size: 2rem;
    }

    .social{
        margin-top: 2rem;
    }

    .social > h4{
        font-size: 4rem;
        margin: 0 1rem;
    }
}


@media screen and (max-width: 760px) {
    .footer-container{
        grid-template-columns: 1fr;
    }

    h4{
        font-size: 1rem;
    }

    p{
        font-size: 1rem;
    }

    .social{
        margin-top: 2rem;
    }

    .social > h4{
        font-size: 2rem;
        margin: 0 1rem;
    }
}