.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.8;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.hero .content h1{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}

.hero .content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

.content .btn{
    margin: 1rem 0.2rem;
}

@media screen and (max-width:  1040px){
    .hero .content > h1{
        font-size: 6rem;
    }
    
    .hero .content p{
        font-size: 2.4rem;
    }

    .content .btn{
       font-size: 2.3rem;
       margin: 1rem 2rem;
    }

    .content > div{
        margin-top: 2rem;
    }

}


@media screen and (max-width:  760px){
    .hero .content > h1{
        font-size: 2.3rem;
    }
    
    .hero .content p{
        font-size: 1rem;
    }

    .content .btn{
       font-size: 1rem;
       margin: 0 1rem;
    }

    .content > div{
        margin-top: 1rem;
    }

}